@use 'variables' as v;
html{
    position: relative;
    box-sizing: border-box;
    font-size: 62.5%;
    scroll-behavior: smooth;
    max-width: 1440px;
    margin:0 auto;
    height: 100vh;
}

*,
*::before,::after{
    box-sizing: inherit;
}

body{
    font-family: v.$fuente_principal;
    font-size: 1.6rem;
    margin: 0;
    padding: 0;

    &.fijar-body{
        position: relative;
        overflow: hidden;
    }
    &body-scroll{
        padding: 8.6rem;
    }
}

p{
    font-size: 2rem;
    color: v.$negro;
    line-height: 1.6;
}

.contenedor{
    width: 95%;
    max-width: 120rem;
    margin: 0 auto;
}

a{
    text-decoration: none;
}

h1,h2,h3{
    margin: 0 0 5rem 0;
}

h1{
    font-size: 4rem;
}

h2{
    font-size: 3.6rem;
}

h3{
    font-size: 3rem;
}

img{
    max-width: 100%;
    width: 100%;
    height: auto;
    display: block;
}

.text-left{
    text-align: left;
}

ul, li{
    list-style: none;

}