@use 'base/variables' as v;
@use 'base/mixins' as m;

.services{
    margin-top: 100rem;
    padding-bottom: 5rem;
    @include m.tablet{
        margin: 50rem 0 0 0;
    }

    .contenido_servicios{
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        gap: 2rem;
        
       

        .card_servicios{
            box-shadow: 1px 1px 10px lighten(v.$negro,50%);
            width: 25rem;
            height: 40rem;
            border-radius: 3px;
            padding: .5rem 1rem;
            display: flex;
            flex-direction: column;
            justify-content: center;
            align-items: center;
            flex-wrap: nowrap;

            h2{
                text-align: center;
                border-bottom: none;
                margin-bottom: 0;
            }
            ul{
                display: flex;
                flex-direction: column;
                gap: 1rem;
                justify-content: start;
                margin: 0 auto;
                padding: 0;
                width: 90%;
                li{
                    margin: 0;
                    padding: 0;
                    text-align: start;
                    justify-content: start;
                }
            }
            p{
                font-size: 1.3rem;
                text-align: center;
                margin-bottom: 5rem;
            }
        }
        .card_servicios:nth-child(2){
            height: 45rem!important;
        }
    }

    @include m.tablet{
        .contenido_servicios{
            display: flex;
            flex-direction: row;
            justify-content: center;
            align-items: center;
            gap: 2rem;
        }
    }

}