@use 'base/variables' as v;
@use 'base/mixins' as m;


.header{
    position: relative;
    width: 100%;
    height: 20rem;
    background-image: url('../../donoyea/banner.jpg');
    background-size:cover;
    background-position-y: center;
    background-color: lighten(v.$negro,40%);



    .layout{
        position: absolute;
        width: 100%;
        height: 100%;
        background-color: hsla(0, 0%, 0%, 0.548);
        transition: all .5s ease-in-out;

        &:hover{
            background-color: hsla(0, 0%, 0%, 0.171);
        }
    }
 
}

.navegacion{
    display: flex;
    flex-direction: column;
    justify-content: space-evenly;
    flex-wrap: wrap;
    padding: 0 1rem;
    transition: all .2s ease-in-out;


    .profile_img{
        position: relative;
        background: gray;
        width: 13rem;
        height: 13rem;
        border-radius: 50rem;
        position: absolute;
        left: 0;
        right: 0;
        top: 2rem;
        margin: auto;
        overflow: hidden;
        border: 1rem solid v.$blanco;
        box-shadow: 3px 3px 15px v.$negro;
        z-index: 60;
    }
    .nav-links{
        display: flex;
        justify-content: center;
        gap: 3rem;
        padding: 0;

        a{
            color: v.$negro;
            font-size: small;
        }
    }
}
@include m.tablet{
    .header{
        height: 40rem!important;
        background-position-y: -20rem;

    }

    .navegacion{
        flex-direction: row;
        padding: 0 10rem;
        .profile_img{
            width: 25rem;
            height: 25rem;
            bottom: 25rem;
        }
        .nav-links {
            li {
                list-style: none;
                padding: 0.5rem;
        
                &:hover {
                    background-color: v.$rosa;
                    cursor: pointer;
        
                    a {
                        color: white; // Cambia el color del enlace a blanco
                    }
                }
            }
        
            a {
                color: v.$negro;
                font-size: medium;
            }
        }
        
    }

}


.seccion{
    margin-top: 5rem;
    padding: 5rem 0 0 0 0;
    h2{
        font-size: 2.5rem;
        border-bottom: 1px solid v.$negro;
    }
}

.artwork-grid{
    display: grid;
    grid-template-columns: repeat(auto-fit,minmax(
        10rem,
        1fr
    ));

    @include m.tablet{
        display: grid;
        grid-template-columns: repeat(auto-fit,minmax(
            30rem,
            1fr
        ));
    }

    gap: .5rem;
    width: 80%;
    margin: 0 auto;
    .box{
        position: relative;
        overflow: hidden;
        width: 100%;
        height: 10rem;
        @include m.tablet{
            height: 30rem;
        }
        border-radius: .5rem;

        &::before{
            position: absolute;
            width: 100%;
            height: 100%;
            content: " ";
            background-color: rgba(0, 0, 0, 0.438);
            z-index: 3;
        }

        img{
            width: 100%;
            height: 100%;
            object-fit: cover;
        }
    }
}

.contenido_about{
    position: absolute;
    left: 0;
    display: flex;
    flex-direction: column;
    width: 100%;
    @include m.tablet{
        display: flex;
        flex-direction: row;
    }
    .card_1{
        width: 100%;
        height: 40rem;
        background: darken(v.$dark, 15%);
        padding: 2rem 3rem;
        height: auto;
        h4{
            color: lighten(v.$orange,1%);
        }
        h1{
            font-size: 2.4rem;
            color: v.$blanco;
            margin-bottom: 0;
        }
        p{
            color: v.$blanco;
            width: 90%;
        }
    }
    .card_2{
        width: 100%;
        height: 40rem;
        background-size: contain;
        background-position: center;
        background-repeat: no-repeat;
        background-color: v.$yellow;
        background-image: url("/donoyea/sinbg.png");
    }
}



.fijo{
    position: fixed;
    z-index: 40;
    background-color: v.$blanco;
    top: 0;
    left: 0;
    width: 100%;
    box-shadow: 1px 1px 3px lighten(v.$negro, 10%);

    .profile_img{
        width: 4rem;
        height: 4rem;
        bottom: 2rem;
        border: 2px solid #fff;
        box-shadow: none;
        transition: none;
    }
}