@use 'base/variables' as v;


.boton{
    font-weight: bold;
    color: v.$blanco;
    border-radius: 50rem;
    padding: 1rem 2rem;
    margin: 2rem auto 0 auto;
    &.boton-comprar{
        background-color: v.$orange;
        
        &:hover{
            background-color: lighten(v.$orange,5%);
        }
    }
}