@use 'base/variables' as v;
@use 'base/mixins' as m;


#galeria{
    display: none;
}

.overlay{
    position: fixed;
    top: 0;
    left: 0;
    bottom: 0;
    right: 0;

    width: 100%;
    height: 100%;

    display: grid;
    place-items: center;

    background-color: rgb(0 0 0 / .5);
    z-index: 99;

    img{
        object-fit: contain;
        height: 80vh;
    }



}

.btn-container{
    position: absolute;
    z-index: 100;
    display: flex;
    flex-direction: row-reverse;
    align-items: center;
    justify-content: space-around;
    gap: 20rem;

    top: 40vh;
    left: 0;

    @include m.tablet{
        gap: 15rem;
    }

    width: 100%;
    .btn{

        position: relative;
        z-index: 99;
        color: rgba(255, 255, 255, 0.822);
        background-color: rgba(0, 0, 0, 0.164);
        padding: 1rem 1.5rem;
        border-radius: 60rem;
        
        &:hover{
            color: rgba(255, 255, 255, 0.959);
            background-color: rgba(0, 0, 0, 0.568);
        }
    }
}