@use 'base/variables' as v;
@use 'base/mixins' as m;

.footer{
    position: relative;
    height: 20rem;
    background-color: lighten(v.$negro, 20%);
    padding: 1.5rem 0;
    margin-top: 5rem;

    .redes{
        display: flex;
        flex-direction: row;
        justify-content: center;
        gap: 3rem;
        list-style: none;
        li{
            width:8rem;
            height:8rem;
            background-color: v.$orange;
            border-radius: 100rem;
            display: flex;
            justify-content: center;
            align-items: center;
            a{
                color: v.$blanco;
                i{
                    font-size: 2.5rem;
                }
            }
        }
    }
    
    span{
        position: absolute;
        width: 100%;
        text-align: center;
        color: darken(v.$blanco, 20%);
        padding: .5rem 0;
        bottom: 0;
        background-color: lighten(v.$negro,10%);
        a{
            color: darken(v.$blanco, 30%);
        }
    }
}