.header {
  position: relative;
  width: 100%;
  height: 20rem;
  background-image: url("../../donoyea/banner.jpg");
  background-size: cover;
  background-position-y: center;
  background-color: #666666;
}
.header .layout {
  position: absolute;
  width: 100%;
  height: 100%;
  background-color: hsla(0, 0%, 0%, 0.548);
  transition: all 0.5s ease-in-out;
}
.header .layout:hover {
  background-color: hsla(0, 0%, 0%, 0.171);
}

.navegacion {
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
  flex-wrap: wrap;
  padding: 0 1rem;
  transition: all 0.2s ease-in-out;
}
.navegacion .profile_img {
  position: relative;
  background: gray;
  width: 13rem;
  height: 13rem;
  border-radius: 50rem;
  position: absolute;
  left: 0;
  right: 0;
  top: 2rem;
  margin: auto;
  overflow: hidden;
  border: 1rem solid #FFFFFF;
  box-shadow: 3px 3px 15px #000000;
  z-index: 60;
}
.navegacion .nav-links {
  display: flex;
  justify-content: center;
  gap: 3rem;
  padding: 0;
}
.navegacion .nav-links a {
  color: #000000;
  font-size: small;
}

@media (min-width: 768px) {
  .header {
    height: 40rem !important;
    background-position-y: -20rem;
  }
  .navegacion {
    flex-direction: row;
    padding: 0 10rem;
  }
  .navegacion .profile_img {
    width: 25rem;
    height: 25rem;
    bottom: 25rem;
  }
  .navegacion .nav-links li {
    list-style: none;
    padding: 0.5rem;
  }
  .navegacion .nav-links li:hover {
    background-color: #F53756;
    cursor: pointer;
  }
  .navegacion .nav-links li:hover a {
    color: white;
  }
  .navegacion .nav-links a {
    color: #000000;
    font-size: medium;
  }
}
.seccion {
  margin-top: 5rem;
  padding: 5rem 0 0 0 0;
}
.seccion h2 {
  font-size: 2.5rem;
  border-bottom: 1px solid #000000;
}

.artwork-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(10rem, 1fr));
  gap: 0.5rem;
  width: 80%;
  margin: 0 auto;
}
@media (min-width: 768px) {
  .artwork-grid {
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(30rem, 1fr));
  }
}
.artwork-grid .box {
  position: relative;
  overflow: hidden;
  width: 100%;
  height: 10rem;
  border-radius: 0.5rem;
}
@media (min-width: 768px) {
  .artwork-grid .box {
    height: 30rem;
  }
}
.artwork-grid .box::before {
  position: absolute;
  width: 100%;
  height: 100%;
  content: " ";
  background-color: rgba(0, 0, 0, 0.438);
  z-index: 3;
}
.artwork-grid .box img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.contenido_about {
  position: absolute;
  left: 0;
  display: flex;
  flex-direction: column;
  width: 100%;
}
@media (min-width: 768px) {
  .contenido_about {
    display: flex;
    flex-direction: row;
  }
}
.contenido_about .card_1 {
  width: 100%;
  height: 40rem;
  background: black;
  padding: 2rem 3rem;
  height: auto;
}
.contenido_about .card_1 h4 {
  color: #f04a4a;
}
.contenido_about .card_1 h1 {
  font-size: 2.4rem;
  color: #FFFFFF;
  margin-bottom: 0;
}
.contenido_about .card_1 p {
  color: #FFFFFF;
  width: 90%;
}
.contenido_about .card_2 {
  width: 100%;
  height: 40rem;
  background-size: contain;
  background-position: center;
  background-repeat: no-repeat;
  background-color: #e9c281;
  background-image: url("/donoyea/sinbg.png");
}

.fijo {
  position: fixed;
  z-index: 40;
  background-color: #FFFFFF;
  top: 0;
  left: 0;
  width: 100%;
  box-shadow: 1px 1px 3px #1a1a1a;
}
.fijo .profile_img {
  width: 4rem;
  height: 4rem;
  bottom: 2rem;
  border: 2px solid #fff;
  box-shadow: none;
  transition: none;
}

.services {
  margin-top: 100rem;
  padding-bottom: 5rem;
}
@media (min-width: 768px) {
  .services {
    margin: 50rem 0 0 0;
  }
}
.services .contenido_servicios {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  gap: 2rem;
}
.services .contenido_servicios .card_servicios {
  box-shadow: 1px 1px 10px gray;
  width: 25rem;
  height: 40rem;
  border-radius: 3px;
  padding: 0.5rem 1rem;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  flex-wrap: nowrap;
}
.services .contenido_servicios .card_servicios h2 {
  text-align: center;
  border-bottom: none;
  margin-bottom: 0;
}
.services .contenido_servicios .card_servicios ul {
  display: flex;
  flex-direction: column;
  gap: 1rem;
  justify-content: start;
  margin: 0 auto;
  padding: 0;
  width: 90%;
}
.services .contenido_servicios .card_servicios ul li {
  margin: 0;
  padding: 0;
  text-align: start;
  justify-content: start;
}
.services .contenido_servicios .card_servicios p {
  font-size: 1.3rem;
  text-align: center;
  margin-bottom: 5rem;
}
.services .contenido_servicios .card_servicios:nth-child(2) {
  height: 45rem !important;
}
@media (min-width: 768px) {
  .services .contenido_servicios {
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    gap: 2rem;
  }
}

.footer {
  position: relative;
  height: 20rem;
  background-color: #333333;
  padding: 1.5rem 0;
  margin-top: 5rem;
}
.footer .redes {
  display: flex;
  flex-direction: row;
  justify-content: center;
  gap: 3rem;
  list-style: none;
}
.footer .redes li {
  width: 8rem;
  height: 8rem;
  background-color: #f04545;
  border-radius: 100rem;
  display: flex;
  justify-content: center;
  align-items: center;
}
.footer .redes li a {
  color: #FFFFFF;
}
.footer .redes li a i {
  font-size: 2.5rem;
}
.footer span {
  position: absolute;
  width: 100%;
  text-align: center;
  color: #cccccc;
  padding: 0.5rem 0;
  bottom: 0;
  background-color: #1a1a1a;
}
.footer span a {
  color: #b3b3b3;
}

#galeria {
  display: none;
}

.overlay {
  position: fixed;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  width: 100%;
  height: 100%;
  display: grid;
  place-items: center;
  background-color: rgba(0, 0, 0, 0.5);
  z-index: 99;
}
.overlay img {
  object-fit: contain;
  height: 80vh;
}

.btn-container {
  position: absolute;
  z-index: 100;
  display: flex;
  flex-direction: row-reverse;
  align-items: center;
  justify-content: space-around;
  gap: 20rem;
  top: 40vh;
  left: 0;
  width: 100%;
}
@media (min-width: 768px) {
  .btn-container {
    gap: 15rem;
  }
}
.btn-container .btn {
  position: relative;
  z-index: 99;
  color: rgba(255, 255, 255, 0.822);
  background-color: rgba(0, 0, 0, 0.164);
  padding: 1rem 1.5rem;
  border-radius: 60rem;
}
.btn-container .btn:hover {
  color: rgba(255, 255, 255, 0.959);
  background-color: rgba(0, 0, 0, 0.568);
}

html {
  position: relative;
  box-sizing: border-box;
  font-size: 62.5%;
  scroll-behavior: smooth;
  max-width: 1440px;
  margin: 0 auto;
  height: 100vh;
}

*,
*::before, ::after {
  box-sizing: inherit;
}

body {
  font-family: "Montserrat", sans-serif;
  font-size: 1.6rem;
  margin: 0;
  padding: 0;
}
body.fijar-body {
  position: relative;
  overflow: hidden;
}
bodybody-scroll {
  padding: 8.6rem;
}

p {
  font-size: 2rem;
  color: #000000;
  line-height: 1.6;
}

.contenedor {
  width: 95%;
  max-width: 120rem;
  margin: 0 auto;
}

a {
  text-decoration: none;
}

h1, h2, h3 {
  margin: 0 0 5rem 0;
}

h1 {
  font-size: 4rem;
}

h2 {
  font-size: 3.6rem;
}

h3 {
  font-size: 3rem;
}

img {
  max-width: 100%;
  width: 100%;
  height: auto;
  display: block;
}

.text-left {
  text-align: left;
}

ul, li {
  list-style: none;
}

.boton {
  font-weight: bold;
  color: #FFFFFF;
  border-radius: 50rem;
  padding: 1rem 2rem;
  margin: 2rem auto 0 auto;
}
.boton.boton-comprar {
  background-color: #f04545;
}
.boton.boton-comprar:hover {
  background-color: #f25d5d;
}